/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */
.wrapper {
  display: flex;
  width: 100%; }

#sidebar {
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  z-index: 999;
  background-color: #505f70;
  background-color: var(--color-primary);
  color: #f8f9fa;
  color: var(--color-light);
  transition: all 0.3s; }

#sidebar.active {
  margin-left: -250px; }

#sidebar .sidebar-header {
  padding: 20px;
  background-color: #505f70;
  background-color: var(--color-primary); }

#sidebar ul.components {
  padding: 20px 0;
  border-bottom: 1px solid #47748b; }

#sidebar ul p {
  color: #f8f9fa;
  color: var(--color-light);
  font-size: 1em;
  font-size: var(--font-size-body); }

#sidebar ul li a {
  padding: 10px;
  font-size: 1em;
  font-size: var(--font-size-body);
  display: block; }

#sidebar ul li a:hover {
  color: #505f70;
  color: var(--color-primary);
  background-color: #f8f9fa;
  background-color: var(--color-light); }

#sidebar ul li.active > a,
a[aria-expanded="true"] {
  color: #f8f9fa;
  color: var(--color-light);
  background-color: #343a40;
  background-color: var(--color-gray-dark); }

a[data-toggle="collapse"] {
  position: relative; }

.dropdown-toggle::after {
  display: block;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%); }

ul ul a {
  font-size: 0.9em !important;
  padding-left: 30px !important;
  background-color: #505f70;
  background-color: var(--color-primary); }

ul.CTAs {
  padding: 20px; }

ul.CTAs a {
  text-align: center;
  font-size: 1em;
  font-size: var(--font-size-body);
  display: block;
  border-radius: 5px;
  margin-bottom: 5px; }

.logo {
  position: relative;
  width: 100%;
  margin-top: 20px; }

/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */
#content {
  width: calc(100% - 250px);
  padding: 40px;
  min-height: 100vh;
  transition: all 0.3s;
  position: absolute;
  top: 0;
  right: 0; }

#content.active {
  width: 100%; }

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */
@media (max-width: 768px) {
  #sidebar {
    margin-left: -250px; }
  #sidebar.active {
    margin-left: 0; }
  #content {
    width: 100%; }
  #content.active {
    width: calc(100% - 250px); }
  #sidebarCollapse span {
    display: none; } }

